* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Courier New",Courier,monospace;
}

input, button {
    appearance: none;
    background: none;
    border: none;
    outline: none;
}

.App {
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #000;
}

form {
    display: block;
    position: relative;
}

form:after {
    content: '';
    display: block;
    position: absolute;
    top: -5px;
    left: -5px;
    right: -5px;
    bottom: -5px;
    z-index: 1;
    background-image: linear-gradient(to bottom right, #830083, #ff00d4 );
}

form .form-inner {
    position: relative;
    display: block;
    background-color: #000;
    padding: 30px;
    z-index: 2;
}

form .form-inner h2 {
    position: relative;
    color: #830083;
    font-size: 28px;
    font-weight: 500;
    padding-bottom: 20px;
}

form .form-inner form-group {
    display: block;
    width: 300px;
    margin-bottom: 15px;
}

.form-inner .form-group label {
    display: block;
    color: #830083;
    font-size: 12px;
    margin-bottom: 15px;
    transition: 0.4s;
}

.form-inner .form-group:focus-within label {
    color: #ff00d4;
}

form .form-inner .form-group input {
    display: block;
    width: 100%;
    padding: 10px 15px;
    background-color: #686868;
    border-radius: 8px;
    transition: 0.4s;
    margin-bottom: 10px;
}

form .form-inner .form-group input:focus {
    box-shadow: 0px 0px 3px rgba (0, 0, 0, 0.2);
}

form .form-inner input[type="submit"], .welcome button {
    display: inline-block;
    padding: 10px 15px;
    border-radius: 8px;
    background-image: linear-gradient(to right, #6b146b, #bb04bb);
    background-size: 200%;
    background-position: 0%;
    transition: 0.4s;
    color: rgb(0, 0, 0);
    font-weight: 700;
    cursor: pointer;
    letter-spacing: 1px;
}

form .form-inner input[type="submit"], .welcome a {
    display: inline-block;
    padding: 10px 3px;
    border-radius: 8px;
    background-size: 200%;
    background-position: 0%;
    transition: 0.4s;
    color: rgb(0, 0, 0);
    font-weight: 700;
    cursor: pointer;
    letter-spacing: 1px;
}

form .form-inner input[type="submit"]:hover {
    background-position: 100% 0%;
}

.error {
    color: #830083;
    margin-bottom: 10px;
}

.welcome {
    width: 100%;
    max-width: 480px;
    background-color: #000;
    padding: 15px 30px;
}

.welcome h2 {
    position: relative;
    color: #888;
    font-size: 28px;
    font-weight: 500px;
    margin-bottom: 30px;
}

.welcome h2 span {
    color: #bb04bb;
    font-weight: 700;
}

.welcome a {
    margin-right: 10px;
}